/**
 * event bus
 */

class EventBus {
  constructor() {
    this.events = this.events || Object.create(null);
  }
  // 
  emit(type, ...args){
    let e;
    e = this.events[type];
    if (Array.isArray(e)) {
      e.map( each => {
        each.apply(this,args);
        return each;
      })
    }
  }

  // 
  on(type,cb){
    const e = this.events[type];
    if(!e){
      this.events[type] = [cb];
    }else{
      e.push(cb)
    }
  }

}

const eventBus = new EventBus();
export default eventBus;


